* {
  margin: 0;
}
html,
body {
  height: 100%;
  padding: 0 !important;
  font-family: 'Montserrat', sans-serif;
}
.page-wrap {
  max-width: 1320px;
  margin: 30px auto !important;
  background-color: #fff;
  padding: 50px;
}
img {
  border: 0;
  max-width: 100%;
  vertical-align: top;
}
a {
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-out 0;
  -moz-transition: all 0.5s ease-out 0;
  -o-transition: all 0.5s ease-out 0;
  transition: all 0.5s ease-out 0;
  color: #007acc;
  text-decoration: underline;
}
a:hover,
a:focus {
  color: #373339;
  text-decoration: none;
}
p {
  margin-bottom: 2rem;
}
h1 {
  font-size: 32px;
  font-weight: 600;
}
h2 {
  font-size: 26px;
  font-weight: 600;
}
h3 {
  font-size: 18px;
  font-weight: 600;
}
h4 {
  font-size: 16px;
}
h5 {
  font-size: 12px;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: montserrat;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.9;
  color: #1a1a1a;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-text-stroke: 1px transparent;
  counter-reset: step-inner-box;
  background-color: #7499bf;
  background-image: url(../images/bodybg.webp);
  background-position: left top;
  background-size: auto;
  background-repeat: repeat;
  background-attachment: scroll;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  box-shadow: none;
  border-radius: 0;
  border-color: #fff;
  background-color: transparent;
  -webkit-text-fill-color: #3a4e57;
  -webkit-box-shadow: none;
  transition: background-color 5000s ease-in-out 0;
}
input::-webkit-input-placeholder {
  color: #a7adb4;
}
input::-moz-placeholder {
  color: #a7adb4;
}
input::-ms-placeholder {
  color: #a7adb4;
}
input.textbox::-ms-input-placeholder {
  color: #a7adb4;
  color: #a7adb4;
}
.bootstrap-select:not([class*='col-']):not([class*='form-control']):not(.input-group-btn) {
  width: 100%;
}
.large-btn,
.medium-btn,
.close-btn,
.close-btn-medium,
.small-btn,
.close-btn-small {
  border-radius: 22px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  padding: 12px 35px;
  background-color: #55aaf5;
  border: none;
  color: #fff;
  -webkit-transition: all 0.5s ease-out 0;
  -moz-transition: all 0.5s ease-out 0;
  -o-transition: all 0.5s ease-out 0;
  transition: all 0.5s ease-out 0;
  text-decoration: none;
  text-transform: capitalize;
  display: inline-block;
}
.large-btn:hover,
.large-btn:focus,
.medium-btn:hover,
.medium-btn:focus,
.close-btn:hover,
.close-btn:focus,
.small-btn:hover,
.small-btn:focus,
.close-btn-small:hover,
.close-btn-small:focus {
  background-color: #3085d0;
  color: #fff;
}
.medium-btn,
.close-btn-medium {
  font-size: 14px;
  padding: 8px 25px;
}
.small-btn,
.close-btn-small {
  font-size: 12px;
  padding: 10px 20px;
}
.close-btn,
.close-btn-medium,
.close-btn-small {
  background-color: #ed63bd;
}
.close-btn:hover,
.close-btn:focus,
.close-btn-medium:hover,
.close-btn-medium:focus,
.close-btn-small:hover,
.close-btn-small:focus {
  background-color: #cf3d9c;
}
textarea {
  resize: none;
}
.control-group {
  display: inline-block;
  vertical-align: top;
  background: #fff;
  text-align: left;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  padding: 30px;
  width: 200px;
  height: 210px;
  margin: 10px;
}
.control {
  display: block;
  position: relative;
  padding-left: 28px;
  margin-bottom: 15px;
  cursor: pointer;
  font-weight: 400;
  line-height: 1.8;
}
.control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.control__indicator {
  position: absolute;
  top: 2px;
  left: 0;
  height: 20px;
  width: 20px;
  background: #fff;
  border: 1px solid silver;
}
.control--radio .control__indicator {
  border-radius: 50%;
}
.control:hover input ~ .control__indicator,
.control input:focus ~ .control__indicator {
  background: #fff;
  border: 1px solid silver;
}
.control input:checked ~ .control__indicator {
  background: #fff;
}
.control:hover input:not([disabled]):checked ~ .control__indicator,
.control input:checked:focus ~ .control__indicator {
  background: #fff;
}
.control input:disabled ~ .control__indicator {
  background: #fff;
  border: 1px solid #d6dde1;
  opacity: 0.6;
  pointer-events: none;
}
.control__indicator:after {
  content: '';
  position: absolute;
  display: none;
}
.control input:checked ~ .control__indicator:after {
  display: block;
  background-color: #ed63bd;
}
.control--checkbox .control__indicator:after {
  left: 6px;
  top: 2px;
  width: 6px;
  height: 11px;
  border: solid #28292b;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.control--checkbox input:disabled ~ .control__indicator:after {
  border-color: #7b7b7b;
}
.control--radio .control__indicator:after {
  left: 3px;
  top: 3px;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background: #fff;
}
.control--radio input:disabled ~ .control__indicator:after {
  background: #7b7b7b;
}
